<script>
import AdvantagesCardItem from '@/components/AdvantagesCardItem.vue';
// import function to register Swiper custom elements
import { register } from 'swiper/element/bundle';
// register Swiper custom elements
register();

export default {
  name: 'App',
  data() {
    return {
      agreeState: false,
      formSendState: false,
      formData: {
        name: '',
        phone: '',
        email: '',
        comment: '',
      },
      fileUploadText: 'Загрузить'
    }
  },
  components: { AdvantagesCardItem },
  methods: {
    checkboxActive() {
      this.agreeState = this.agreeState ? false : true
      let btn = document.querySelector('.project__btn--send')
      if (this.agreeState) {
        btn.removeAttribute('disabled')
        btn.setAttribute('style', 'background: #E35029; cursor: pointer')
        btn.classList.toggle('active-send')
      } else {
        btn.setAttribute('disabled', 'true')
        btn.setAttribute('style', 'background: #E2E7F1; cursor: default')
        btn.classList.toggle('active-send')
      }
    },
    sendForm() {
      this.formSendState = this.formSendState ? false : true
      this.formData.name = ''
      this.formData.phone = ''
      this.formData.email = ''
      this.formData.comment = ''
    },
    fileUpload() {
      this.fileUploadText = 'Загружено'
      let btn = document.querySelector('.project__btn--upload')
      btn.setAttribute('disabled', 'true')
      btn.setAttribute('style', 'background: #BFC8DC; cursor: default')
    },
    test() {
      document.querySelector('body').classList.toggle('fixed-page')
      document.querySelector('.burger').classList.toggle('open-menu')
      document.querySelector('.header__nav').classList.toggle('active')
    }
  }
}
</script>

